.buttonGlowOnHover {
  width: 200px;
  height: 50px;
  margin: 20px;
  /* border: none; */
  border-radius: 5px;
  outline: none;
  color: #fff;
  font-size: 1.2em;
  /* background: rgb(230, 200, 200); */
  cursor: pointer;
  position: relative;
  z-index: 0;
  font-family: "Girassol", "Arial", cursive;
}

.buttonGlowOnHover:before {
  content: "";
  background: linear-gradient(
    45deg,
    #ff0000,
    #ff7300,
    #fffb00,
    #48ff00,
    #00ffd5,
    #002bff,
    #7a00ff,
    #ff00c8,
    #ff0000
  );
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing 20s linear infinite;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
}

.buttonGlowOnHover:active {
  color: #000;
}

.buttonGlowOnHover:active:after {
  background: transparent;
}

.buttonGlowOnHover:hover:before {
  opacity: 1;
}

.buttonGlowOnHover:after {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgb(32, 9, 94);
  left: 0;
  top: 0;
}

@keyframes glowing {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}
.seeAllMemesButton {
  margin-top: 10%;
}
